.Container_Navbar{
  background-color: #225bd7;
  height: 40px;
}

.Navbar_Button{
  height: 100%;
}

.Navbar_Button > button {
  margin-top: 4px;
  background-color: #579d52;
  border: 1px solid #579d52;
  border-left: 0px;
  border-top-left-radius: 10px;
  border-top-right-radius: 12px;
  border-bottom-right-radius: 18px;
  width: 10%;
  height: 90%;
  cursor: pointer;
  color: #fff;
  font-weight: 600;
  align-items: center;
  justify-content: center;
  display: flex;
  font-size: 18px;
  font-family: italic;;
  box-shadow: inset -1px 4px 10px 1px #00310a;
  text-shadow: -3px 2px 6px #000;
  
}

.Navbar_Button > button > p{
  transform: skewY(0deg);
}

.Navbar_Button > button > img {
  margin-right: 5px;
  height: 30px;
  width: 30px;
}